import { Component, trigger, transition, state, style, animate, ViewChild} from '@angular/core';
import { NavController, NavParams, Searchbar } from 'ionic-angular';
import { ApiService } from '../../providers/api-service';
import { OnInit } from '@angular/core/src/metadata/lifecycle_hooks';
import { Storage } from '@ionic/storage';
import {  ApiError, Account } from '../../interfaces/api-model';
import 'rxjs/add/operator/mergeMap';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Platform } from 'ionic-angular/platform/platform';
/**
 * Generated class for the MainPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
@Component({
  selector: 'page-main',
  templateUrl: 'main.html',
  animations: [
    trigger('addAccountTrigger', [
      state('invisible', style({ opacity: '0', display: 'none' })),
      state('visible', style({ opacity: '1', display: 'flex' })),
      transition('visible => invisible', animate('200ms ease-in')),
      transition('invisible => visible', animate('200ms ease-out'))
    ]),
    trigger('stepTriggerTwo', [
      state('invisible', style({ transform: 'translate(100%, -100%)' })),
      state('visible', style({ transform: 'translate(0%, -100%)' })),
      transition('visible => invisible', animate('300ms ease-in')),
      transition('invisible => visible', animate('300ms ease-out'))
    ]),
    trigger('stepTriggerOne', [
      state('invisible', style({ transform: 'translateX(0%)' })),
      state('visible', style({  transform: 'translateX(-100%)' })),
      transition('visible => invisible', animate('300ms ease-in')),
      transition('invisible => visible', animate('300ms ease-out'))
    ])
  ]
})
export class MainPage implements OnInit{

  accCreate: FormGroup;
  accCode: string = "";
  progressDisplayNone: string = 'none';
  customeLogin: string = 'Account name';

  private token: string = "";
  private accountsList: Array<Account> = [];

  private addAccountVisibility: string = 'invisible';
  private stepTransaction: string = 'invisible'
  private listVisibility: string = 'visible';
  items: Array<string> = [];
  itemsBackUp: Array<string> = [];

  str: string = '';
  str2: string = '';
  str3: string = '';

  searchTerm: string = '';

  @ViewChild('searchBar') searchBar: Searchbar;

  constructor(public navCtrl: NavController, public navParams: NavParams,
    private apiService: ApiService, private storage: Storage, formBuilder: FormBuilder, private platform: Platform) {
    this.accCreate = formBuilder.group({
      accName: ['', Validators.compose([Validators.required])]
    });
    this.accCreate.valueChanges.subscribe(_value => this.clearForm())  
    // this.items.push('btc');
    // this.items.push('eth');
    // this.items.push('usd');
    // this.items.push('igrc')
    this.initItems();
  }
  initItems(){
    this.items = []
    this.items.push('btc');
    this.items.push('eth');
    this.items.push('usd');
    this.items.push('igrc');
    this.itemsBackUp = this.items;
    console.log("this items back up", this.itemsBackUp);
  }
  getItems(event: any){
    // console.log(event);
    // this.items.filter(event =>{

    // })
    this.refreshList();
    var value = event.target.value;
    if (value && value.trim() != '') {
      this.items = this.items.filter((item) => {
        return (item.toLowerCase().indexOf(value.toLowerCase()) > -1);
    })
  }
  }
  refreshList(){
    this.items = this.itemsBackUp;
  }
  clearForm(){

  }
  // .mergeMap((_account: Account)=> this.apiService.getUserAccounts(this.token))
  createAccountForUser(accname: string){
    console.log(accname);
    this.apiService.createAccount(accname, this.accCode, this.token)
    .subscribe( (account) => {
      console.log(account);
      this.stepTransaction = 'invisible';
      // this.accountsList = accounts
      // console.log("new list", this.accountsList);
      // this.addAccountVisibility = 'invisible';
      // this.listVisibility = 'visible';
    },
    (error: ApiError) => {
      console.log(error);
    });
  }

  ngOnInit(){
    // .mergeMap((_result: Boolean) => this.apiService.login(login, password))
    let token: string;
   this.storage.get("USER_TOKEN")
    .then( _token =>{
      token = _token;
      this.token = _token;
      // console.log("token ", _token);
      this.apiService.getUserAccounts(_token)
    .subscribe((accounts: Array<Account>) => {
      console.log(accounts);
      this.accountsList = accounts;
    },
    (error: ApiError) => {
      console.log(error);
    });
    })
    .catch(error=>{
      console.log(error);
    })
  }

  addAccount(){
    this.addAccountVisibility = 'visible';
    this.listVisibility = 'invisible';
  }
  onCancel(){
    this.accCreate.controls.accName.setValue('');
    this.stepTransaction = 'invisible';
    this.items = this.itemsBackUp;
    // this.listVisibility = 'visible';
  }

  getUserAccounts(){
    this.apiService.getUserAccounts(this.token)
    .subscribe((accounts: Array<Account>) => {
      // console.log(accounts);
      this.accountsList = accounts;
      console.log("this.accountList ", this.accountsList);
    },
    (error: ApiError) => {
      console.log(error);
    });
  }

  ionViewDidLoad() {
    if(this.platform.is('mobileweb')){
      console.log("this is mobileweb");
      this.str = 'mobileweb';
    }
    if(this.platform.is('mobile')){
      console.log("mobile");
      this.str2 = 'mobile';
    }
    if(this.platform.is('phablet')){
      console.log("phablet");
    }
    if(this.platform.is('tablet')){
      console.log("tablet");
    }
    if(this.platform.is('windows')){
      console.log("windows");
    }
    if(this.platform.is('iphone')){
      console.log("iphone");
    }
    if(this.platform.is('ipad')){
      console.log("ipad");
    }
    if(this.platform.is('ios')){
      console.log("ios");
    }
    if(this.platform.is('core')){
      console.log("core");
    }
    if(this.platform.is('cordova')){
      console.log("cordova");
    }
    if(this.platform.is('android')){
      console.log("android");
      this.str3 = 'android';
    }
  }

  goToSecondStep(code: string){
    this.searchBar.value = '';
    this.accCode = code;
    console.log(this.accCode);
    this.stepTransaction = 'visible';
  }
}



