import { Component, Input, forwardRef, AfterViewInit, OnChanges, ViewChild, trigger, state, transition, animate, style } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor, FormControl } from '@angular/forms';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import { Events } from 'ionic-angular/util/events';

// import { Keyboard } from '@ionic-native/keyboard';


// export const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR: any = {
  
// };

const CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => InputFloatingComponent),
  multi: true
};
@Component({
  selector: 'input-floating',
  providers:[CUSTOM_INPUT_CONTROL_VALUE_ACCESSOR],
  templateUrl: 'input-floating.html',
  animations: [
    trigger('heightChange', [
      state('collapsed', style({ transform: 'scaleY(0.0)', height: '0', display: 'none' })),
      state('expanded', style({ transform: 'scaleY(1.0)', height: '*', display: 'flex' })),
      transition('collapsed <=> expanded', animate('150ms')),
    ]),
    trigger('scaleChange', [
      state('collapsed', style({ transform: 'scale(1.0)', color: '#A8A8A8' })),
      state('expanded', style({ transform: 'scale(0.8) translateX(-11.5%)', color: '#488AFF',  fontWeight: '500', paddingTop: '0px'})),
      transition('collapsed <=> expanded', animate('150ms ease-out')),
    ]),
    trigger('opacityState', [
      state('visible', style({ opacity: '1' })),
      state('invisible', style({ opacity: '0' })),
      transition('visible <=> invisible', animate('150ms ease-out')),
    ]),
  ]
})
export class InputFloatingComponent implements ControlValueAccessor, AfterViewInit, OnChanges {

  @Input() type = "text";
  @Input() progressDisplay = "";
  @Input() checkCollapse: Subject<boolean>;
  @Input() letterSpacing = "";
  @Input() force: boolean = false;
  @Input() text = "";
  @Input() hint: string = "Hint";
  @Input() hintError: string = "";
  @Input() hintErrorColor: string = "#FF0000";
  @Input() c: FormControl = new FormControl();
  @Input() hintColor: string = "#A8A8A8";
  @Input() accentColor: string = "#A8A8A8";
  @Input() backgroundColorOverlay: string = "#FFFFFF";
  @Input() progressWidth: string = '0%';
  @Input() border: string = "";
  
  // @Input('value') innerValue = '';  
  errors: Array<any> = ['This field is required'];

  inputState: string = 'collapsed';
  hintErrorState: string = 'invisible';
  @Input() spacing: boolean = true;

  private _value: any = '';

  // get reference to the input element
  @ViewChild('input') inputRef: any;


  constructor(private events: Events) {
    // private keyboard: Keyboard
    // this.keyboard.hideKeyboardAccessoryBar
    // this.keyboard.onKeyboardShow().subscribe(
    //   next =>{
    //     console.log("onNext ", next);
    //   },
    //   error =>{
    //     console.log("onError ", error);
    //   },
    //   () => {
    //     console.log("onComplete");
    //   }
    // )
    // this.checkCollapse.subscribe()
    events.subscribe('close:inputs', () => {
      this.collapse();
    });
  }

  onFocusOut() {
    if(this.force == true) {
            console.log("Must be collapsed");
            // console.log("must be ")
      this.inputState = 'collapsed';      
      this.hintErrorState = 'invisible';
    }
    console.log("On focus out " + this._value);
    if(!this._value) {
      this.inputState = 'collapsed';      
      this.hintErrorState = 'invisible';
    }
    this.inputState = 'collapsed';      
    this.hintErrorState = 'invisible';
    // if(this._value == ""){
    //   this.inputState = 'collapsed';      
    //   this.hintErrorState = 'invisible';
    // }
  }

  onFocus() {
    this.inputState = 'expanded';
    if(this.hintError){
      this.hintErrorState = 'visible';      
    }
  }

  ngOnChanges() {
   
  }

  expand() {
    this.inputState = 'expanded';
    if(this.hintError){
      this.hintErrorState = 'visible';      
    }
    // this.onFocus();
    setTimeout(() => {
      this.inputRef.setFocus();
    }, 10);
  }

  collapse() {
    console.log("On focus out " + this._value);
    if(!this._value) {
      this.inputState = 'collapsed';      
      this.hintErrorState = 'invisible';
    }
  }

  ngAfterViewInit() {

    // RESET the custom input form control UI when the form control is RESET
    this.c.valueChanges.subscribe(
      () => {
        // check condition if the form control is RESET
        if (this.c.value == "" || this.c.value == null || this.c.value == undefined) {
          // this.innerValue = "";
          this.inputRef.nativeElement.value = "";
          // this.onFocusOut();
          // this.collapse();
        }
      }
    );
    // if(this.checkCollapse){
    //   this.checkCollapse.subscribe(
    //     (next)=>{
    //       if(next == true){
    //         // this.collapse();
    //       }
    //     }
    //   );
    // }
  }
  get value(): any { return this._value; };

  set value(v: any) {
    if (v !== this._value) {
      this._value = v;
      this.onChange(v);
    }
  }
  // write the value to the input
  writeValue(value: any) {
    this._value = value;
    this.onChange(value);
  }

  onChange = (_:any) => {};
  onTouched = () => {};
  registerOnChange(fn: (_: any) => void): void { this.onChange = fn; }
  registerOnTouched(fn: () => void): void { this.onTouched = fn; }

  propagateChange = (_: any) => { }
}


