import { ApiService } from './../providers/api-service';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { CustomIconsModule } from 'ionic2-custom-icons';
import { ScrollToModule } from '@nicky-lenaers/ngx-scroll-to';
import { Keyboard } from '@ionic-native/keyboard';
import { Content } from 'ionic-angular';
// import { Keyboard } from '@ionic-native/keyboard'; need discuss
// for live site removes # from URL 
    //also needs a line in nginx.conf = location / { try_files $uri /index.html; }
//import { LocationStrategy, PathLocationStrategy, APP_BASE_HREF } from '@angular/common';

import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';

import { InAppBrowser } from '@ionic-native/in-app-browser';
import { SplashScreen } from '@ionic-native/splash-screen';

import { IonicStorageModule } from '@ionic/storage';

import { xCoinsApp } from './app.component';

import { UserData } from '../providers/user-data';

import {HttpClientModule, HttpClient} from '@angular/common/http';
import {TranslateModule, TranslateLoader} from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';
import { ScreenOrientation } from '@ionic-native/screen-orientation';
import { MainPage } from '../pages/main/main';
import { ListPage } from '../pages/list/list';
import { LoginPageModule } from '../pages/login/login.module';
import { AccountsPage } from '../pages/accounts/accounts';
import { DashboardPage } from '../pages/dashboard/dashboard';
import { ExchangePage } from '../pages/exchange/exchange';
import { BalancesPage } from '../pages/balances/balances';
import { TutorialPage } from '../pages/tutorial/tutorial';
// import { AccountsPage } from '../pages/accounts/accounts';
// import { List } from 'ionic-angular/components/list/list';

export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http);
}

export let language: string = 'en_US';

export function setLanguage(lang: string) {
  language = lang;
}

@NgModule({
  declarations: [
    xCoinsApp,
    MainPage,
    ListPage,
    AccountsPage,
    DashboardPage,
    ExchangePage,
    BalancesPage,
    TutorialPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    BrowserAnimationsModule,
    LoginPageModule,    
    IonicModule.forRoot(xCoinsApp,{
      scrollAssist: false, 
      autoFocusAssist: false
    }, 
{
  links: [
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module#HomePageModule', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] }
  ]
}),
    ScrollToModule.forRoot(),    
    CustomIconsModule, // Add this!
    HttpClientModule,    
    TranslateModule.forRoot({
      loader: {
          provide: TranslateLoader,
          useFactory: HttpLoaderFactory,
          deps: [HttpClient]
      }
  }),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    xCoinsApp,
    MainPage,
    ListPage,
    AccountsPage,
    DashboardPage,
    ExchangePage,
    BalancesPage,
    TutorialPage
    
  ],
  providers: [
    { provide: ErrorHandler, useClass: IonicErrorHandler }, /*{--removes # from url-- provide: LocationStrategy, useClass: PathLocationStrategy }, {provide: APP_BASE_HREF, useValue: '/'},*/
    UserData,
	  InAppBrowser,
    SplashScreen,
    ApiService,
    ScreenOrientation,
    Keyboard,
    Content
  ]
})
export class AppModule { }



