import { AccessToken, ApiError, ErrorType, User, Account } from './../interfaces/api-model';
import { Injectable } from '@angular/core';
import { Http, RequestOptions, Headers } from '@angular/http';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import { Observable } from 'rxjs/Observable';


@Injectable()
export class ApiService {

  private url: string = "http://13.56.148.150:5000"
  private debugLoggingEnabled = true;

  constructor(private http: Http) {
  }

  private log(response: Response | any) {
    if (this.debugLoggingEnabled) {
      console.log(response);
    }
  }

  private handleError(error: Response | any) {
    var apiError: ApiError = {
      code: error.status,
      message: error.statusText,
    };
    switch (error.status) {
      case 401:
        apiError.type = ErrorType.NotAuthorized;
        break;
      default:
        apiError.type = ErrorType.ServerError;
    }
    console.log(apiError);
    return Observable.throw(apiError);
  }

  login(login: string, password: string): Observable<AccessToken> {
    return this.http.post(this.url + '/api/User/Login',
      {
        site: "xCoins",
        login: login,
        password: password
      })
      .map(res => res.json())
      .do(this.log)
      .catch(this.handleError);
  }

  checkName(name: string) : Observable<Boolean> {
    return this.http.get(this.url + '/api/User/CheckName/'+ name)
    .map(res => res.json())
    .do(this.log)
    .catch(this.handleError);
  }

  register(login: string, password: string): Observable<Boolean> {
    return this.http.post(this.url + '/api/User/Add',
    {
      siteName: "xCoins",
      login: login,
      locale: "en_US",
      status: "A",
      password: password
      
    })
    .do(this.log)
    .catch(this.handleError)
    .map(_response => true);
  }

  getUser(token: string): Observable<User> {
    let headers = new Headers();
    headers.append('Authorization', 'Bearer ' + token);
    let options = new RequestOptions({ headers: headers });
    return this.http.get(this.url + '/api/User/Get', options)
    .map(res => res.json())
    .do(this.log)
    .catch(this.handleError);
  }

  getUserAccounts(token: string): Observable<Array<Account>>{
    let headers = new Headers();
    headers.append('Authorization', 'Bearer ' + token);
    let options = new RequestOptions({ headers: headers });
    return this.http.post(this.url + '/api/Account/List',
    {
      first: "0"      
    }, options)
    .map(res => res.json())
    .do(this.log)
    .catch(this.handleError);
  }

  createAccount(accName:string, code: string, token: string) : Observable<Account> {
    let headers = new Headers();
    headers.append('Authorization', 'Bearer ' + token);
    let options = new RequestOptions({ headers: headers });
    return this.http.post(this.url + '/api/Account/Create',
    {
      assetCode: code,
      accountName: accName  
    }, options)
    .map(res => res.json())
    .do(this.log)
    .catch(this.handleError);
  }

}



