export interface AccessToken {
  validTo: string
  value: string
}


export enum ErrorType {
  NotAuthorized,
  ServerError,
  Unknown,
}

export interface ApiError {
  type ? : ErrorType
  message ? : string
  code ? : number
}

export interface User {
  login ? : number;
  nameFirst: string;
  nameLast: string;
  email: string;
  phone ? : string;
  authenticatorSecret ? : string;
  verificationCode ? : string;
  locale ? : string;
  account ? : string;
  timeAdded ? : string;
  status ? : string;
  siteName ? : string;
}

export interface Account {
  name?: string,
  balanceTotal?: number,
  balanceReserved?: number,
  balancePending?: number,
  companyId?: number,
  createdBy?: number,
  number?: string,
  timeAdded?: string,
  assetCode?: string
}



